<template>
    <div class="tab">
        <div class="tab-top"></div>
        <div class="tab-box">
            <nav
                :class="
                    item.id === curTab ? 'tab-box-item now' : 'tab-box-item'
                "
                v-for="(item, index) in tab"
                @click="handleTabNav(item.id)"
                :key="index"
            >
                {{ item.title }}
            </nav>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tab: [
                { id: 1, title: "产品分类" },
                { id: 2, title: "商城首页" },
                { id: 3, title: "产品大全" },
                { id: 4, title: "企业中心" },
                { id: 5, title: "企业后台" },
            ],
            curTab: 1,
        };
    },
    methods:{
        handleTabNav(id){
            this.curTab=id;
        }
    }
};
</script>

<style lang="less" scoped>
.tab {
    width: 100%;
    height: 80px;
    background: #ffffff;
    box-shadow: 0px 8px 10px 0px rgba(128, 101, 101, 0.15);
    border-bottom: 1px solid #2e8afb;
    &-top {
        width: 100%;
        height: 30px;
    }
    &-box {
        width: 1400px;
        margin: 0 auto;
        display: flex;
        align-items: flex-end;
        line-height: 50px;
        &-item {
            width: 200px;
            height: 50px;
            text-align: center;
            font-size: 20px;
            font-family: MicrosoftYaHei-Bold, MicrosoftYaHei;
            color: #666666;
            cursor: pointer;
        }
        .now {
            color: #ffffff;
            background: #2e8afb;
        }
    }
}
</style>