<template>
    <div class="nav">
        <div class="nav-box">
            <div class="nav-box-left">
                <nav
                    :class="
                        item.id === curTab
                            ? 'nav-box-left-item now'
                            : 'nav-box-left-item'
                    "
                    v-for="(item, index) in tab"
                    @click="handleTabNav(item.id)"
                    :key="index"
                >
                    <span
                        :class="
                            item.id == curTab
                                ? 'nav-box-left-item-img newspan'
                                : 'nav-box-left-item-img'
                        "
                    >
                        <img :src="item.id == curTab ? item.now : item.icon" />
                    </span>
                    <label> {{ item.title }}</label>
                </nav>
            </div>
            <div class="nav-box-right">
                <div
                    v-for="(item, index) in tab"
                    :key="index"
                    v-show="item.id == curTab"
                >
                    <img src="../assets/image/banner.png" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import icon_1 from "../assets/image/icon-1.png";
import icon_1_1 from "../assets/image/icon-1-1.png";
import icon_2 from "../assets/image/icon-2.png";
import icon_2_2 from "../assets/image/icon-2-2.png";
import icon_3 from "../assets/image/icon-3.png";
import icon_3_3 from "../assets/image/icon-3-3.png";
import icon_4 from "../assets/image/icon-4.png";
import icon_4_4 from "../assets/image/icon-4-4.png";
import icon_5 from "../assets/image/icon-5.png";
import icon_5_5 from "../assets/image/icon-5-5.png";
export default {
    data() {
        return {
            tab: [
                { id: 1, title: "智能手机", icon: icon_1, now: icon_1_1 },
                { id: 2, title: "空调设备", icon: icon_2, now: icon_2_2 },
                { id: 3, title: "电视影音", icon: icon_3, now: icon_3_3 },
                { id: 4, title: "3C电子", icon: icon_4, now: icon_4_4 },
                { id: 5, title: "笔记本/台式机", icon: icon_5, now: icon_5_5 },
            ],
            curTab: 1,
        };
    },
    methods: {
        handleTabNav(id) {
            this.curTab = id;
        },
    },
};
</script>

<style lang="less" scoped>
.nav {
    width: 100%;
    height: 596px;
    background: #ffffff;
    &-box {
        width: 1400px;
        height: 500px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;

        box-shadow: 0px 8px 10px 0px rgba(128, 101, 101, 0.15);
        &-left {
            width: 300px;
            height: 100%;
            &-item {
                width: 100%;
                height: 100px;
                line-height: 100px;
                font-size: 18px;
                font-family: MicrosoftYaHei;
                position: relative;
                cursor: pointer;
                &-img {
                    display: inline-block;
                    width: 26px;
                    height: 30px;
                    position: absolute;
                    top: 38px;
                    left: 30px;
                    border-bottom: 3px solid #000;
                    img {
                        width: 20px;
                        height: 20px;
                        position: absolute;
                        left: 3px;
                        top: 2px;
                    }
                }
                label {
                    box-sizing: border-box;
                    padding-left: 60px;
                }
            }
            .now {
                color: #2e8afb;
                background: #d5e8fe;
                .newspan {
                    border-bottom: 3px solid #2e8afb;
                }
            }
        }
    }
}
</style>